import React from 'react';
import { PrimaryButton } from '@fluentui/react';
import './App.css';

export default function App() {
    const [count, setCount] = React.useState(0);

  return (
    <div className="App">
      <p>mmmmmmmmmmm... {count}</p>
        <PrimaryButton onClick={() => setCount(count + 1)}>
            Click me
        </PrimaryButton>
    </div>
  );
}
